.header {
    font-family: "Regular";
    display: flex;
    justify-content: space-between;

    &__left {
        display: flex;
    }

    &__right {
        display: flex;
    }
}

.logo {
    height: 6rem;

    &:hover {
        cursor: pointer;
    }
}

.contact-items-box {
    display: flex;
    flex-direction: column;
}

.contact-icons {
    height: 3rem;

    &:hover {
        cursor: pointer;
    }
}

.hamburger {
    font-size: 3rem;

    &:hover {
        cursor: pointer;
    }
}